$primaryColor: #4527a4;

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Block Directory
.wp-block-business-card {
	text-align: center;

	* {
		box-sizing: border-box;
	}

	.bcbBusinessCard {
		// width: 560px;
		// height: 320px;
		max-width: 100%;
		display: inline-block;

		&.theme1,
		&.theme2 {
			display: inline-flex;
			align-items: center;
			justify-content: space-between;

			.contacts {
				max-width: 50%;
			}
		}

		&.theme2 {
			align-items: flex-start;
			flex-direction: row-reverse;
		}

		.header {
			display: grid;
			height: min-content;

			.name,
			.title {
				margin: 0 0 5px 0;
				text-align: initial;
				width: auto;
			}

			.separator {
				text-align: initial;
				display: inline-block;
			}
		}

		.contacts {
			.contact {
				display: flex;
				align-items: center;
				gap: 16px;
				padding: 0;

				&:not(:last-child) {
					margin-bottom: 8px;
				}

				.icon {
					font-size: 18px;
					font-style: normal;
					width: 34px;
					height: 34px;
					background: #ddd;
					padding: 8px;
					border-radius: 50%;
				}

				.text {
					width: calc(100% - 50px);
					margin: 0;
					text-align: left;
				}
			}
		}
	}
}